import * as React from "react";
import { createRef } from 'react';
import { hot } from "react-hot-loader";
import { withRouter } from "react-router-dom";
import './Map.scss';
import { mapbox } from "../mapbox";
import 'mapbox-gl/dist/mapbox-gl.css';
import { Typography, Row, Col, Button } from 'antd';

const { Title } = Typography;

type MapViewState = {
  eventState: EventState,
  mapBox: any,
  mapContainerRef: HTMLDivElement,
  lng: number,
  lat: number,
  zoom: number,
  isCreatingEvent: boolean,
  canCreateEvent: boolean,
}

type EventState = {
  routeCoordinates: Array<Number>,
  clickCoordinates: Array<Number>,
  clicks: number,
  routeInstructions: Map<number, any>,
}

//https://github.com/firebase/firebaseui-web-react
//https://github.com/alex3165/react-mapbox-gl
class MapView extends React.Component<Record<string, unknown>, MapViewState> {
  public readonly state: MapViewState = {
    isCreatingEvent: false,
    canCreateEvent: false,
    eventState: {
      routeCoordinates: [],
      clickCoordinates: [],
      clicks: 0,
      routeInstructions: new Map<number, any>(),
    },
    mapBox: null,
    mapContainerRef: null,
    lng: -122.2946,
    lat: 37.1787,
    zoom: 14,
  }

  private mapInstanceRef = createRef<HTMLDivElement>();
  private eventCreationZoomMinLevel = 11.7;

  constructor(props) {
    super(props);

    this.cancelEventCreationHandler = this.cancelEventCreationHandler.bind(this);
    this.saveEventHandler = this.saveEventHandler.bind(this);
    this.onMapMove = this.onMapMove.bind(this);
    this.onMapClick = this.onMapClick.bind(this);
    this.onLoadWalkingRequest = this.onLoadWalkingRequest.bind(this);
  }

  private getFeatureCollectionDescription(coords) {
    return {
      'type': 'FeatureCollection',
      'features': [
        {
          'type': 'Feature',
          'properties': {},
          'geometry': {
            'type': 'Point',
            'coordinates': coords
          }
        }
      ]
    };
  }

  private getMapLayerDescription(name, coords, color) {
    return {
      'id': name,
      'type': 'circle',
      'source': {
        'type': 'geojson',
        'data': this.getFeatureCollectionDescription(coords),
      },
      'paint': {
        'circle-radius': 10,
        'circle-color': color
      }
    };
  }

  private lngLatDictToArray(coordsObj) {
    var coords = Object.keys(coordsObj).map(function (key) {
      return coordsObj[key];
    });
    return coords;
  }


  public componentDidMount() {
    const map = new mapbox.Map({
      container: this.mapInstanceRef.current,
      //style: 'mapbox://styles/mapbox/outdoors-v11',
      style: {
        version: 8,
        sources: {
          osm: {
            type: 'raster',
            tiles: ["https://tile.openstreetmap.org/{z}/{x}/{y}.png"],
            tileSize: 256,
            attribution: 'Map tiles by <a target="_top" rel="noopener" href="https://tile.openstreetmap.org/">OpenStreetMap tile servers</a>, under the <a target="_top" rel="noopener" href="https://operations.osmfoundation.org/policies/tiles/">tile usage policy</a>. Data by <a target="_top" rel="noopener" href="http://openstreetmap.org">OpenStreetMap</a>'
          }
        },
        layers: [{
          id: 'osm',
          type: 'raster',
          source: 'osm',
        }]
      },
      center: [this.state.lng, this.state.lat],
      zoom: this.state.zoom
    });

    map.on('move', (e) => this.onMapMove(map, e));
    map.on('click', (e) => this.onMapClick(map, e));

    this.setState({ mapBox: map });
  }

  private onMapMove(map, e) {

    this.setState({
      lat: map.getCenter().lat.toFixed(4),
      lng: map.getCenter().lng.toFixed(4),
      zoom: map.getZoom().toFixed(2),
      canCreateEvent: this.eventCreationZoomMinLevel < this.state.zoom
    });
  }

  private onMapClick(map, e) {
    if (this.eventCreationZoomMinLevel < map.getZoom()) {
      if (!this.state.isCreatingEvent) {
        this.setState({
          isCreatingEvent: true,
          eventState: {
            routeCoordinates: [],
            clicks: 0,
            clickCoordinates: [],
            routeInstructions: new Map<number, any>()
          }
        })
      }

      var coords = this.lngLatDictToArray(e.lngLat);
      map.getCanvasContainer().style.cursor = '';

      if (map.getLayer('end')) {
        var end = this.getFeatureCollectionDescription(coords);
        map.getSource('end').setData(end);
      } else {
        map.addLayer(this.getMapLayerDescription('start', coords, '#f30'));
        map.addLayer(this.getMapLayerDescription('end', [], '#3887be'));
      }
      this.getRoute(coords);
    }
  }

  private onLoadWalkingRequest(e) {
    var json = JSON.parse(e.target.response);
    var data = json.routes[0];
    var route = data.geometry.coordinates;

    for (const r in route) {
      this.state.eventState.routeCoordinates.push(route[r]);
    }

    var geojson = {
      'type': 'Feature',
      'properties': {},
      'geometry': {
        'type': 'LineString',
        'coordinates': this.state.eventState.routeCoordinates
      }
    };
    // if the route already exists on the map, we'll reset it using setData
    if (this.state.mapBox.getSource('route')) {
      this.state.mapBox.getSource('route').setData(geojson);
    }
    // otherwise, we'll make a new request
    else {
      this.state.mapBox.addLayer({
        'id': 'route',
        'type': 'line',
        'source': {
          'type': 'geojson',
          'data': {
            'type': 'Feature',
            'properties': {},
            'geometry': {
              'type': 'LineString',
              'coordinates': this.state.eventState.routeCoordinates
            }
          }
        },
        'layout': {
          'line-join': 'round',
          'line-cap': 'round'
        },
        'paint': {
          'line-color': '#3887be',
          'line-width': 5,
          'line-opacity': 0.75
        }
      });
    }

    this.state.eventState.clicks += 1;
    this.state.eventState.routeInstructions.set(this.state.eventState.clicks, data.legs[0]);

    this.setState({eventState: this.state.eventState});

    // this.state.eventState.clicks = eventState.clicks + 1;
    // this.state.eventState.routeInstructions.set(eventState.clicks, data.legs[0]);

    // setEventState({
    //   clickCoordinates: eventState.clickCoordinates,
    //   routeCoordinates: eventState.routeCoordinates,
    //   clicks: eventState.clicks,
    //   routeInstructions: eventState.routeInstructions,
    // });

    // console.log(eventState);

    //console.log(data);
    // get the sidebar and add the instructions
    //var instructions = document.getElementById('instructions');
    //var steps = data.legs[0].steps;

    // var tripInstructions = [];
    // for (var i = 0; i < steps.length; i++) {
    //   tripInstructions.push('<br><li>' + steps[i].maneuver.instruction) +
    //     '</li>';
    //   //instructions.innerHTML =
    //   console.log(
    //     '<br><span class="duration">Trip duration: ' +
    //     Math.floor(data.duration / 60) +
    //     ' min ðŸš´ </span>' +
    //     tripInstructions);
    // }
  }

  private getRoute(end) {

    this.state.eventState.clickCoordinates.push(end[0]);
    this.state.eventState.clickCoordinates.push(end[1]);

    const clicksCount = this.state.eventState.clickCoordinates.length;
    const prevPoint = clicksCount == 2 ? clicksCount - 1 : clicksCount - 3;

    // make directions request using cycling profile
    var url =
      'https://api.mapbox.com/directions/v5/mapbox/walking/' +
      this.state.eventState.clickCoordinates[prevPoint - 1] +
      ',' +
      this.state.eventState.clickCoordinates[prevPoint] +
      ';' +
      end[0] +
      ',' +
      end[1] +
      '?steps=true&geometries=geojson&access_token=' +
      mapbox.accessToken;

    // make an XHR request https://developer.mozilla.org/en-US/docs/Web/API/XMLHttpRequest
    var req = new XMLHttpRequest();
    req.open('GET', url, true);
    req.onload = this.onLoadWalkingRequest;
    req.send();
  }

  private cancelEventCreationHandler() {
    this.setState({
      isCreatingEvent: false,
      eventState: {
        clickCoordinates: [],
        clicks: 0,
        routeInstructions: new Map<number, any>(),
        routeCoordinates: []
      }
    });

    this.state.mapBox.removeLayer('route');
    this.state.mapBox.removeSource('route');

    this.state.mapBox.removeLayer('start');
    this.state.mapBox.removeSource('start');

    this.state.mapBox.removeLayer('end');
    this.state.mapBox.removeSource('end');
  }

  private saveEventHandler() {

  }

  public render() {
    let body = (<></>);
    if (this.state.isCreatingEvent) {
      let summary = null;
      let distance = 0;
      for(let [key, instruction] of this.state.eventState.routeInstructions) {
        if(instruction.summary && summary === null) {
          summary = instruction.summary;
        }
        distance += instruction.distance;
      }

      if(summary === null || summary == '') {
        summary = "New event";
      }


      body = (
        <Row>
          <Col span={24}>
            <Title level={2}>{summary}</Title>
          </Col>
          <Col span={24}>
            Distance: {(distance/1000).toFixed(2)} km
          </Col>
          <Col span={24}>
            <Button onClick={this.cancelEventCreationHandler}>Cancel</Button>
            <Button onClick={this.saveEventHandler}>Save</Button>
          </Col>
        </Row>
      );
    } else {
      if (this.state.canCreateEvent) {
        body = (
          <Row>
            <Col span={24}>
              <Title level={2}>Explore upcoming hikes or Click to create your event</Title>
            </Col>
          </Row>
        );
      }
      else {
        body = (
          <Row>
            <Col span={24}>
              <Title level={2}>Explore upcoming hikes or Zoom in to create your event</Title>
            </Col>
          </Row>
        );
      }
    }

    return (
      <>
        <Row>
          <Col span={18}>
            <div className='map-container' ref={this.mapInstanceRef} />
          </Col>
          <Col span={6}>
            {body}
          </Col>
        </Row>
      </>
    );
  }
}

declare let module: Record<string, unknown>;

export default hot(module)(withRouter(MapView));
